body{
  background-color: #e8ebeb;
}
table{
  border-collapse: collapse;
  width: 100%;
  height: 100%;
}

td,th{
  padding: 8px;
}

tr:nth-child(even){
  background-color: #f2f2f2;
}
tr:nth-child(odd){
  background-color: white;
}

.sidebar{
  position: fixed;
  left: 0;
  top: 0;
  background-color: white;
  width: 5%;
  height: 100%;
}

.Table{
  position: absolute;
  top: 15%;
  left: 8%;
  display: block;
  width: 92%;
  height: auto;
}

.sidenav a {
  padding: 10px 8px 6px 1px;
  text-decoration: solid;
  font-size: 15px;
  color: black;
  display: block;
}

.sidenav a:hover {
  color: #f1f1f1;
}

.navbar{
  width: 95%;
  height: 7%;
  position: absolute;
  top: 0;
  left: 5%;
  background-color: white;
}

.search{
  border-radius: 10px;
  height: 1.5rem;
  font-size: medium;
  margin-bottom: 1%;
  position: absolute;
  top: 9%;
  right: 2%;
}

.profile{

  position: absolute;
  top: 10px;
  right: 2%;
}

.homeicon{
  padding-left: 12%;
}
.sidebaricon{
  position: absolute;
  bottom: 2%;
  padding-left: 12%;
  
}

@media only screen and (max-width: 600px){
  .sidebar{
    width: 10%;
  }

  .Table{
    left: 15%;
    width: 85%;
  }
}